$desktopIconSize: 3rem;

.desktopIcons {
    display: flex;
    flex-direction: column;
    margin-right: 2rem;

    & > :not(:last-child) {
        margin-bottom: 2rem;
    }
}

.folderIcon {
    width: $desktopIconSize;
}

.browserIcon {
    width: $desktopIconSize;
    margin-bottom: .5rem;
}